<template>
  <div>
    <div class="spinner-bar" v-if="spinner_active">
      <div class="mdl-spinner mdl-js-spinner is-active" style="margin: 20px;"></div>
    </div>
    <div class="mdl-grid">
      <div class="mdl-cell mdl-cell--8-col">
        <div class="picture">
          <img :src="this.manga.url" />
        </div>
        <div class="content" v-html="this.manga.content">
          {{ this.manga.content }}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Imager from 'imager.js'
import network from '../networkUtil'
export default {
  data () {
    return {
      'manga': [],
      'spinner_active': true
    }
  },
  mounted () {
    var contextHolder = this
    var getManga = function (key) {
      return new Promise((resolve, reject) => {
        var statement = key + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'
        resolve('https://www.googleapis.com/blogger/v3/blogs/' + process.env.BLOG_CODE + '/posts/' + statement)
      })
    }
    getManga(this.$route.params.id)
      .then((url) => network.promisedRequest('GET', url))
      .then((rawResponse) => {
        contextHolder.manga = JSON.parse(rawResponse)
        contextHolder.manga.content = contextHolder.manga.content.replace(new RegExp('/s1600/', 'g'), '/s{width}/')
        contextHolder.manga.content = contextHolder.manga.content.replace(new RegExp('src', 'g'), 'data-src')
        contextHolder.manga.content = contextHolder.manga.content.replace(new RegExp('<img', 'g'), '<img class="responsive-image"')
      })
      .then(() => {
        /* eslint-disable no-new */
        new Imager('.responsive-image', { availableWidths: [320, 400, 500, 600, 700, 800, 900, 1000, 1200, 1400, 1600] })
      })
      .then(() => { contextHolder.spinner_active = false })
  }
}
</script>
<style scoped>
  .info {
    text-align: right;
    padding: 5px;
    color: #555;
    font-size: 10px;
  }
  .spinner-bar {
    text-align: center;
  }
</style>


