<template>
  <div>
    <div class="spinner-bar" v-if="spinner_active">
      <div class="mdl-spinner mdl-js-spinner is-active" style="margin: 20px;"></div>
    </div>
    <div class="mdl-grid">
      <div class="mdl-cell mdl-cell--4-col" v-for="picture in this.pictures" v-if="network.online || picture.cached">
        <div class="image-card mdl-card mdl-cell--middle mdl-shadow--2dp" @click="displayDetails(picture.id, picture.cached)" v-bind:style="'background : url('+picture.image+') center/cover'">
          <div class="mdl-card__title mdl-card--expand" v-bind:class="{ 'mdl-card--notavailable' : !picture.cached }"></div>
          <div class="mdl-card__actions">
            <span class="image-card__name">{{ picture.title }}</span>
          </div>
          <div class="mdl-card__menu">
            <button v-if="!picture.cached" class="mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect">
              <i class="material-icons">cloud_download</i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <router-link class="add-picture-button mdl-button mdl-js-button mdl-button--fab mdl-button--colored" to="/post">
      <i class="material-icons">add</i>
    </router-link>
  </div>
</template>
<script>
  import network from '../networkUtil'
  export default {
    methods: {
      displayDetails (id, cached) {
        if (this.network.online || cached) {
          this.$router.push({name: 'detail', params: { id: id }})
        }
      }
    },
    data () {
      return {
        'pictures': [],
        'spinner_active': true,
        'network': network
      }
    },
    mounted () {
      var contextHolder = this
      // Fetch the latest data.
      var resolveURL = function (paginate) {
        return new Promise((resolve, reject) => {
          console.log('[pagination]' + paginate == null)
          var statement = ''
          if (paginate == null) {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true'
          } else {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true&pageToken=' + paginate
          }
          resolve('https://www.googleapis.com/blogger/v3/blogs/' + process.env.BLOG_CODE + '/posts' + statement)
        })
      }
      function fetchList (paginate) {
        return resolveURL(paginate)
          .then((url) => network.promisedRequest('GET', url))
          .then((rawResponse) => {
            var response = JSON.parse(rawResponse)
            console.log(response)
            response.items.forEach((postAbstract) => {
              if (postAbstract.images[0] != null) {
                postAbstract.image = postAbstract.images[0].url.replace('/s1600/', '/s400/') // downsample image size
              }
              postAbstract.cached = false
              if ('caches' in window) {
                caches.keys().then((keys) => Promise.all(
                  keys.map((cacheName) => caches.open(cacheName).then((cache) => cache.match(postAbstract.selfLink + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'))
                    .then((response) => {
                      if (typeof response !== 'undefined') {
                        postAbstract.cached = true
                      }
                    })
                  )
                ))
              }
              contextHolder.pictures.push(postAbstract)
            })
            if (response.nextPageToken != null) {
              return fetchList(response.nextPageToken)
            }
          })
      }
      fetchList().then(() => { contextHolder.spinner_active = false })
    }
  }
</script>
<style scoped>
  .add-picture-button {
    position: fixed;
    right: 24px;
    bottom: 24px;
    z-index: 998;
  }
  .image-card {
    position: relative;
    height: 350px;
    width: 256px;
    margin-bottom: 8px;
  }
  .mdl-card--notavailable {
    background: rgba(0, 0, 0, 0.5);
  }
  .image-card > .mdl-card__actions {
    padding: 16px;
    background: rgba(0, 0, 0, 0.5);
  }
  .image-card > .mdl-card__menu {
    color: #fff;
  }
  .image-card__name {
    color: #fff;
    font-size: 14px;
    font-weight: 500;
  }
  .spinner-bar {
    text-align: center;
  }
</style>


